export function replace_PANTOID(string){
    if (Utilities.getMasterData().int_version == 1) {
        const PAN_TEXT = PAN || '';
        const PAN_TO_ID_TEXT = PAN_TO_ID || '';
        const regexPAN = new RegExp(PAN_TEXT, "g");
        return string.replace(regexPAN, PAN_TO_ID_TEXT);
    } else {
        return string
    }
}

